.ShippingBanner {
  position: relative;
  z-index: 500
}
.ShippingBanner-body {
  background-color: #252525;
  clear: both;
  color: #fffef2;
  height: auto;
  overflow: visible;
  padding: 5px 0px 2px;
  top: 0;
  width: 100%;
  z-index: 2
}

.ShippingBanner-body .BannerContent {
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row
}

.ShippingBanner-content {
  font-family: 'DM Sans', sans-serif;
  -ms-flex-positive: 2;
  flex-grow: 2;
  font-size: .85rem;
  line-height: 1.0;
  text-align: center
}

.ShippingBanner-content:hover{
  line-height: 1.0;
  text-decoration: underline;
}

html[lang=jp] .ShippingBanner-content {
  font-family: Hiragino Kaku Gothic Pro, Meiryo, sans-serif
}

.topnav {
  background-color: #fffef0;
  overflow: hidden;
  padding: 0 40px 0 40px; 
}

/* Style the links inside the navigation bar */
.topnav a {
  font-family: 'DM Sans', sans-serif;
  float: left;
  display: block;
  color: black;
  text-align: center;
  padding: 30px 16px;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
}

/* Change the color of links on hover */
.topnav a:hover {
  background-color: #ddd;
  color: black;
}

/* Add an active class to highlight the current page */

/* Hide the link that should open and close the topnav on small screens */
.topnav .icon {
  display: none;
}

.topnav-right {
  float: right;
}
